.infoBox{
    flex: 1;
}
.infoBox:not(:last-child){
    margin-right: 10px;
}
.infoBox--selected{
    border-top: 10px solid #d24dff;
}
.infoBox--red{
    border-top:red;
}
.infoBox_cases{
    color: #cc1034;
    font-weight: 600;
    font-size: 1.75rem;
    margin-bottom: 0.5rem;
}
.info_total{
    color: #6c757d;
    font-weight: 700 !important;
    font-size: 0.8rem !important;
    margin-top: 15px !important ;
}
